	var app = getApp().globalData
    var content = null;
    var touchs = [];
    var canvasw = 0;
    var canvash = 0;
    var _that;
    //获取系统信息
    uni.getSystemInfo({
        success: function(res) {
            canvasw = res.windowWidth;
            canvash = res.windowHeight;
        },
    })
    export default {
        data() {
            return {
				id:'',
                signImage: '',
                isEnd: false // 是否签名
            }
        },
        methods: {
            overSign: function() {
                if (this.isEnd) {
                    uni.canvasToTempFilePath({
                        canvasId: 'firstCanvas',
						fileType:'png',
                        success: function(res) {
                            //打印图片路径
                            // console.log(res.tempFilePath)
                            // console.log('完成签名')
                            //设置图片
                            _that.signImage = res.tempFilePath
							
							uni.uploadFile({
								  url: app.aliUrl,
								  filePath: _that.signImage,
								  name: 'file',
								  formData: {
									  key:"wx/"+"${filename}",
									  signature: app.signature,
									  policy:app.policy,
									  OSSAccessKeyId:app.OSSAccessKeyId,
									  success_action_status:'201'
								  }
						 }).then(res=>{
								// console.log(res)
								//  console.log(res[1].data.split('<Location>')[1].split("</Location>")[0])
								 if(res[1].statusCode == "201"){
									 _that.$api.Clubmanage_agree({
									 	token: uni.getStorageSync("token"),
									 	user_id: uni.getStorageSync("userId"),
									 	id:_that.id,
										image:res[1].data.split('<Location>')[1].split("</Location>")[0]
									 }).then(res=>{
									 	if(res.data.code=="200"){
											uni.showToast({
											    title: '签名成功',
											    mask: "true",
												icon:'none'
											});
											setTimeout(function() {
												uni.navigateBack({
												    delta: 1
												});
											}, 500);
										}
									 })
								 }
								
						 })
                        }
                    })
                } else {
                    uni.showToast({
                        title: '请在灰色区域签自己的名字',
                        icon: "none",
                        duration: 1500,
                        mask: true
                    })
                }

            },

            // 画布的触摸移动开始手势响应
            start: function(event) {
                // console.log(event)
                // console.log("触摸开始" + event.changedTouches[0].x)
                // console.log("触摸开始" + event.changedTouches[0].y)
                //获取触摸开始的 x,y
                let point = {
                    x: event.changedTouches[0].x,
                    y: event.changedTouches[0].y
                }
                // console.log(point)
                touchs.push(point);

            },
            // 画布的触摸移动手势响应
            move: function(e) {
                let point = {
                    x: e.touches[0].x,
                    y: e.touches[0].y
                }
                // console.log(point)
                touchs.push(point)
                if (touchs.length >= 2) {
                    this.draw(touchs)
                }
            },

            // 画布的触摸移动结束手势响应
            end: function(e) {
                // console.log("触摸结束" + e)
                // 设置为已经签名
                this.isEnd = true
                // 清空轨迹数组
                for (let i = 0; i < touchs.length; i++) {
                    touchs.pop()
                }

            },

            // 画布的触摸取消响应
            cancel: function(e) {
                // console.log("触摸取消" + e)
            },

            // 画布的长按手势响应
            tap: function(e) {
                // console.log("长按手势" + e)
            },

            error: function(e) {
                // console.log("画布触摸错误" + e)
            },

            //绘制
            draw: function(touchs) {
                // console.log(touchs[0],touchs[1])
                let point1 = touchs[0]
                let point2 = touchs[1]
                touchs.shift()
                content.moveTo(point1.x, point1.y)
                content.lineTo(point2.x, point2.y)
                content.stroke()
                content.draw(true)
            },
            //清除操作
            clearClick: function() {
                // 设置为未签名
                this.isEnd = false
                //清除画布
                content.clearRect(0, 0, canvasw, canvash)
                content.draw(true)
            },
        },
        /**
         * 生命周期函数--监听页面加载
         */
        onLoad: function(options) {
			this.id = options.id
            _that = this
            //获得Canvas的上下文
            content = wx.createCanvasContext('firstCanvas')
            //设置线的颜色
            content.setStrokeStyle("#000")
            //设置线的宽度
            content.setLineWidth(5)
            //设置线两端端点样式更加圆润
            content.setLineCap('round')
            //设置两条线连接处更加圆润
            content.setLineJoin('round')
        },

    }

